import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const Main = (resolve) => {
  import('@/main/main').then((module) => {
    resolve(module)
  })
}

// 首页
const HelloWorld = (resolve) => {
  import('@/components/HelloWorld').then((module) => {
    resolve(module)
  })
}

// 提示注册页面
const PromptRegistration = (resolve) => {
  import('@/views/registration/promptRegistration').then((module) => {
    resolve(module)
  })
}

// 注册页面
const register = (resolve) => {
  import('@/views/registration/register').then((module) => {
    resolve(module)
  })
}

// 医院介绍
const hospitalInfo = (resolve) => {
  import('@/views/hospitalInfo/hospitalInfo').then((module) => {
    resolve(module)
  })
}

// 专家介绍
const expertIntroduction = (resolve) => {
  import('@/views/expertIntroduction/expertIntroduction').then((module) => {
    resolve(module)
  })
}

// 在线预约
const doctorVerb = (resolve) => {
  import('@/views/doctorVerb/doctorVerb').then((module) => {
    resolve(module)
  })
}

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    // 提示注册页面
    {
      path: '/PromptRegistration',
      name: 'PromptRegistration',
      component: PromptRegistration
    },
    // 注册页面
    {
      path: '/register',
      name: 'register',
      component: register
    },
    // 医院介绍
    {
      path: '/hospitalInfo',
      name: 'hospitalInfo',
      component: hospitalInfo
    },
    // 专家介绍
    {
      path: '/',
      name: 'expertIntroduction',
      component: Main,
      children: [
        {
          path: 'expertIntroduction',
          component: expertIntroduction
        }
      ]
    },
    // 在线预约
    {
      path: '/',
      name: 'doctorVerb',
      component: Main,
      children: [
        {
          path: 'doctorVerb',
          component: doctorVerb
        }
      ]
    }
  ]
})



